import { useEffect, useState } from 'react';
import ChecklistList from './ChecklistList';

// Watch #12 for filtering -> search

const Home = () => {

    const [checklists, setChecklists] = useState([
        {id: 1, title: "Example 1", items: [
            {id: 1, parent: 0, name: "item1", checked: false},
            {id: 2, parent: 1, name: "item2", checked: false},
            {id: 3, parent: 0, name: "item3", checked: false},
            {id: 4, parent: 0, name: "item4", checked: true},
        ]},
        {id: 2, title: "Example 2", items: [
            {id: 1, parent: 0, name: "item1", checked: false},
            {id: 2, parent: 0, name: "item2", checked: false},
        ]}
    ]);

    const handleDelete = (id) => {
        const newChecklists = checklists.filter(checklist => checklist.id !== id);
        setChecklists(newChecklists);
    }

    useEffect(() => {}, [])
    // Fires on every render, depending what's in []

    return ( 
        <div className="home">
            <ChecklistList checklists={checklists} handleDelete={handleDelete}/>
        </div>
     );
}
 
export default Home;